import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ResponseJSON } from 'src/app/storage/main-storage/storage.model';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  constructor(private http: HttpClient) { }

  getStorages(): Observable<string[]> {
    return this.http.get<string[]>(`${environment.api}/storages`);
  }

  deleteFile(name: string): Observable<ResponseJSON> {	
		return this.http.delete<ResponseJSON>(`${environment.api}/storages/delete?filename=${name}`);
  }

}
